<template>
  <div class="biz-content">
    <biz-sector-list @sectorClick="getClassBySector" title="选择业务板块" :sectors="sectors"></biz-sector-list>
    <div id="custom_latitude">
      <div class="tableSector">
        <el-row :gutter="20">
          <el-col :span="24">
            <div class="grid-content bg-purple-light">
              <div class="tag">
                <el-tag
                  v-for="item in customerDimensionList"
                  :key="item.code"
                  closable
                  :type="currentDimension===item.code?'primary':'info'"
                  @close="removeDimension(item.code)"
                  @click.native="dimensionDetail(item.code)">
                  {{item.name}}
                </el-tag>
                <el-button size="small" plain icon="el-icon-plus" @click="dimensionDialogVisible=true">新增维度</el-button>
                <el-button type="text" icon="iconfont icon-bianji" size="small" @click="dialogTableVisible = true" style="float: right;">编辑</el-button>
              </div>
              <el-table :data="currentDimensionDetail" stripe :header-cell-style="tableHeader" height="400px">
                <el-table-column header-align="center" prop="no" label="序号" min-width="50" align="center"></el-table-column>
                <el-table-column header-align="center" prop="tag" label="标签" min-width="200" align="center"></el-table-column>
                <el-table-column header-align="center" prop="range" label="标签范围" min-width="250" align="center" v-if="rangeShow"></el-table-column>
                <el-table-column header-align="center" prop="down" label="从()" min-width="125" align="center" v-if="!rangeShow"></el-table-column>
                <el-table-column header-align="center" prop="up" label="至(不含)" min-width="125" align="center" v-if="!rangeShow"></el-table-column>
                <el-table-column header-align="center" prop="a" label="满期赔付率（%）" min-width="140" align="center"></el-table-column>
                <el-table-column header-align="center" prop="b" label="合并终极赔付率（%）" min-width="160" align="center"></el-table-column>
                <el-table-column header-align="center" prop="c" label="保费占比（%）" min-width="120" align="center"></el-table-column>
              </el-table>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="option">
      <back-button>上一步</back-button>
      <back-button type="next" @next="confirm">下一步</back-button>
    </div>

    <el-dialog title="编辑维度" fullscreen :visible.sync="dialogTableVisible">
      <template v-for="record in currentDimensionData">
        <tag-dimension v-if="record.type==='tag'" :key="record.id" :data="record"></tag-dimension>
        <range-dimension v-if="record.type==='range'" :key="record.id" :data="record"></range-dimension>
      </template>
      <div slot="footer" style="text-align: center" class="dialog-footer">
        <el-button type="primary" plain @click="dialogTableVisible = false">保存纬度</el-button>
        <el-button type="primary" plain @click="dialogTableVisible = false">关闭</el-button>
      </div>
    </el-dialog>

    <el-dialog title="添加维度" :visible.sync="dimensionDialogVisible" width="30%">
      <!--<p>已选: <el-tag type="success">已选</el-tag> 待选: <el-tag>待选</el-tag></p>-->
      <el-tag
        v-for="item in dimensionList"
        :key="item.code"
        :type="currentCustomDimension.includes(item.code)? 'primary': 'info'"
        @click.native="addDimension(item.code)">
        {{item.name}}
      </el-tag>
    </el-dialog>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {customDimension} from '@/api/individual'

const BackButton = () => import('@/components/BackButton')
const BizSectorList = () => import('./BizSectorList')
const TagDimension = () => import('./TagDimension')
const RangeDimension = () => import('./RangeDimension')

export default {
  name: 'CustomDimension',
  components: {BackButton, BizSectorList, TagDimension, RangeDimension},
  data () {
    return {
      baseRecommend: [], // 智能推荐结果数据
      currentSector: 0, // 当前选中的板块数组索引
      dimensionList: [], // 当前选中板块的所有维度数据
      currentCustomDimension: [], // 当前选中板块的自定义维度信息
      currentDimensionDetail: [], // 当前选择的维度的详细切分信息
      currentDimension: '',
      dimensionColumns: [ // 维度切分信息表头
        {prop: 'no', title: '序号', width: 50, align: 'center'},
        {prop: 'tag', title: '标签', width: 200, align: 'center'},
        {prop: 'range', title: '标签范围', width: 128, align: 'center'},
        {prop: 'a', title: '满期赔付率（%）', width: 140, align: 'center'},
        {prop: 'b', title: '合并终极赔付率（%）', width: 160, align: 'center'},
        {prop: 'c', title: '保费占比（%）', width: 120, align: 'center'}
      ],
      currentDimensionData: [
        {
          id: 'comLastCount',
          type: 'tag',
          title: '商业险上年出险次数',
          tags: [
            {code: 'noCom', name: '未投保商业险'},
            {code: 'newCar', name: '新车'},
            {code: 'newIns', name: '新保'},
            {code: 'no3', name: '连续3年+未出险'},
            {code: 'no2', name: '连续2年未出险'},
            {code: 'no1', name: '上年未出险'},
            {code: '1', name: '1次'},
            {code: '2', name: '2次'},
            {code: '3', name: '3次'},
            {code: '4', name: '4次'},
            {code: '5', name: '5次+'}
          ],
          split: [
            {
              id: '1',
              title: '未投保商业险',
              tags: ['noCom']
            },
            {
              id: '2',
              title: '0次',
              tags: ['newCar', 'newIns', 'no3', 'no2', 'no1']
            },
            {
              id: '3',
              title: '1次',
              tags: ['1']
            },
            {
              id: '4',
              title: '2次',
              tags: ['2']
            },
            {
              id: '5',
              title: '3次及以上',
              tags: ['3', '4', '5']
            }
          ]
        },
        {
          id: 'age',
          type: 'range',
          title: '年龄',
          list: [
            {
              id: '1',
              isEdit: false,
              title: '20以下',
              start: '0',
              end: '20'
            },
            {
              id: '2',
              isEdit: false,
              title: '20-30',
              start: '20',
              end: '30'
            },
            {
              id: '3',
              isEdit: false,
              title: '30-45',
              start: '30',
              end: '45'
            },
            {
              id: '4',
              isEdit: false,
              title: '45-60',
              start: '45',
              end: '60'
            },
            {
              id: '5',
              isEdit: false,
              title: '60以上',
              start: '60',
              end: 'N'
            }
          ]
        },
        {
          id: 'rci',
          type: 'range',
          title: '商业险上年出险金额',
          list: [
            {
              id: '1',
              isEdit: false,
              title: '1000以下',
              start: '0',
              end: '1000'
            },
            {
              id: '2',
              isEdit: false,
              title: '1000-2000',
              start: '1000',
              end: '2000'
            },
            {
              id: '3',
              isEdit: false,
              title: '2000-5000',
              start: '2000',
              end: '5000'
            },
            {
              id: '4',
              isEdit: false,
              title: '5000-10000',
              start: '5000',
              end: '10000'
            },
            {
              id: '5',
              isEdit: false,
              title: '10000以上',
              start: '10000',
              end: 'N'
            }
          ]
        }
      ],
      dialogTableVisible: false,
      dimensionDialogVisible: false,
      rangeShow: true
    }
  },
  created () {
    customDimension(this.param).then(response => {
      this.baseRecommend = response.data
      // 设置板块数据源
      const sectorData = response.data.map(item => {
        return {id: item.id, name: item.name}
      })
      this.$store.commit('SET_SECTORS', sectorData)
      this.loadDimension()
      this.currentDimension = response.data[0].dimensionList[0].code
      this.currentDimensionDetail = response.data[0].dimensionList[0].detail
    })
  },
  computed: {
    ...mapState({
      sectors: state => state.individual.sectors,
      param: state => state.individual.param,
      tableHeader: state => state.app.tableHeader
    }),
    customerDimensionList () {
      return this.currentCustomDimension.map(id => {
        return this.dimensionList.find(item => item.code === id)
      })
    },
    leftDimensionList () {
      return this.dimensionList.filter(item => {
        return !this.currentCustomDimension.includes(item.code)
      })
    }
  },
  methods: {
    loadDimension () {
      this.dimensionList = this.baseRecommend[this.currentSector].dimensionList.map(item => {
        return {code: item.code, name: item.name}
      })
      this.currentCustomDimension = this.baseRecommend[this.currentSector].recommendDimension
    },
    getClassBySector (id) { // 选择板块后处理事件
      this.currentSector = this.sectors.findIndex(item => item.id === id)
      this.loadDimension()
    },
    addDimension (id) { // 添加维度
      if (!this.currentCustomDimension.includes(id)) {
        this.currentCustomDimension.push(id)
        if (id === 'forceLastPay') {
          this.currentDimensionData.push({
            id: 'forceLastPay',
            type: 'range',
            title: '交强险上年出险金额',
            list: []
          })
        }
      }
    },
    removeDimension (id) { // 删除选择的自定义维度
      const index = this.currentDimensionData.findIndex(item => item.id === id)
      this.currentDimensionData.splice(index, 1)
      this.currentCustomDimension.splice(this.currentCustomDimension.indexOf(id), 1)
    },
    dimensionDetail (code) { // 点击所选维度后，展示对应维度的详情
      this.currentDimension = code
      const currentDimension = this.baseRecommend[this.currentSector].dimensionList.find(item => item.code === code)
      this.rangeShow = currentDimension.rangeShow
      this.currentDimensionDetail = currentDimension.detail
    },
    confirm () {
      this.$router.push({path: '/admin/individual/declare/confirm'})
    }
  }
}
</script>

<style scoped>
  .tableSector{
    background: #fff;
    margin: 10px auto;
  }
  .m-top-md{
    margin-top:10px;
  }
  .el-table td, .el-table th{
    padding: 6px 0;
  }
  .el-tag{
    margin-left: 10px;
    margin-bottom: 5px;
  }
  .sectorBox{
    padding:0;
    margin:0;
    border:1px solid #ebeef5;
    max-height: 280px;
    overflow-y: auto;
  }
  .sectorBox li{
    list-style-type: none;
    line-height: 38px;
    border-bottom:1px solid #ebeef5;
    font-size:13px;
    color: #606266;
    padding: 0 15px;
    cursor: pointer;
  }
  .tag{
    display: block;
    margin-bottom: 16px;
  }
  .tagItem{
    margin-top: 10px;
  }
  .edit{
    text-align: right;
    display: block;
  }
  .sectorBox li:hover{
    background: #ebeef5;
  }

</style>



